import { Component, OnInit, ViewChild } from '@angular/core';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { SpectDetail } from '../../_models/SpectDetail';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-manage-hdd',
  templateUrl: './manage-hdd.component.html',
  styleUrls: ['./manage-hdd.component.css']
})
export class ManageHddComponent implements OnInit {
  model: any = {};
  hdds: SpectDetail[];
  roleid: any =  Number(localStorage.getItem('RoleID'));
  @ViewChild('detailModal') public detailModal: ModalDirective;
  @ViewChild('EditModal') public EditModal: ModalDirective;

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadHDDs();
  }

  loadHDDs() {
    return this.loadspecsService.loadSpectDetails().subscribe((hdds: SpectDetail[]) => {
      this.hdds = hdds.filter(proj => (proj.parentId === 2));
    }, error => {
      this.alertify.error(error);
    });
  }

  AddHDD() {
    this.model.ParentId = 2;
    if (!this.model.Text) {
      this.alertify.error('Enter HDD');
      return;
    }
    this.loadspecsService.Addspecs(this.model).subscribe((hdds: SpectDetail[]) => {
      this.hdds = hdds.filter(proj => (proj.parentId === 2));
      this.alertify.success('HDD Added Successful');
      this.detailModal.hide();
      this.model.Text = '';
    }, error => {
      this.alertify.error(error);
    });
  }

  EditHDD(id: number, Text: string) {
    this.model.Id = id;
    this.model.Text = Text;
    this.EditModal.show();
  }

  EditHDDPost() {
    if (!this.model.Text) {
      this.alertify.error('Enter HDD');
      return;
    }
    this.loadspecsService.Editspecs(this.model).subscribe((hdds: SpectDetail[]) => {
      this.hdds = hdds.filter(proj => (proj.parentId === 2));
      this.alertify.success('HDD Edited Successful');
      this.EditModal.hide();
    }, error => {
      this.alertify.error(error);
    });

  }

}
